import { processYAML } from "../../../processMarkdown/yaml";
import { parseMarkdown } from "../../../processMarkdown/parseMarkdown";
import { createCalendar } from "../../createCalendar";

// Fonction debounce pour gÃ©rer l'update du contenu avec un dÃ©lai
function debounce(func, wait) {
	let timeout;
	return function (...args) {
		const later = () => {
			clearTimeout(timeout);
			func(...args);
		};
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
	};
}

// Fonction pour mettre Ã  jour les cartes
export async function updateContent() {
	const editorElement = document.querySelector(".editor");
	const editorContent =
		editorElement && editorElement.textContent ? editorElement.textContent : "";
	const md = editorContent ? await processYAML(editorContent) : "";
	const data = md.trim() ? parseMarkdown(md) : null;
	return data ? createCalendar(data, undefined) : null;
}

export function eventKeyUpDebounceUpdateContent() {
	// Utiliser debounce pour appeler updateContent avec un dÃ©lai afin d'Ã©viter un lag dans le cas d'un document long
	const editorElement = document.querySelector(".editor");
	const debouncedUpdateContent = debounce(async () => {
		await updateContent();
	}, 600);
	editorElement.addEventListener("keyup", () => {
		debouncedUpdateContent();
	});
}
