import { loadCSS } from "../../utils/urls";
import { initMarkdownEditor } from "./editor";
import { updateContent } from "./helpers/updateContent";
import { resetYamlToDefault } from "../../processMarkdown/yaml";

export function initEditorButtonEvents(params) {
	const openEditorButton = document.querySelector("button.openEditor");
	if (openEditorButton) {
		// On lance l'Ã©diteur si on clique sur le bouton "ouvrir l'Ã©diteur"
		openEditorButton.addEventListener("click", () => {
			initEditor();
		});
		// On lance l'Ã©diteur s'il y a le paramÃ¨tre "editor" dans l'URL
		if (params.editor !== undefined) {
			initEditor();
		}
	}
}

function initEditor() {
	// On teste si on est sur tÃ©lÃ©phone ou petit Ã©cran
	if (window.innerWidth < 600 || window.innerHeight < 500) {
		alert(
			"L'Ã©diteur Markdown n'est pas disponible sur les petits Ã©crans. Merci d'utiliser un ordinateur ou une tablette en mode paysage.",
		);
		return;
	}
	resetYamlToDefault();
	document.body.classList.add("editMode");
	const editorWrapper = document.querySelector(".editor-wrapper");
	loadCSS("../css/editor.min.css", "editor");
	if (editorWrapper === null) {
		initMarkdownEditor();
	} else {
		editorWrapper.style.display = "block";
		const closeEditorButton = document.querySelector(".close-editor-button");
		closeEditorButton.style.display = "block";
		updateContent();
	}
}
